import { Component, OnInit } from '@angular/core';
import { Animation, AnimationController, ModalController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';
@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.page.html',
  styleUrls: ['./checkout.page.scss'],
})
export class CheckoutPage implements OnInit {

 
  datastorage: any;
  name : any;
  uid: any;  
  cart: any =[];
  pname:any;
  street: any;
  brgy: any;
  cont_num: any;
  userdata: any;
  sum: any;
  total: any;


  constructor(
    private animatioCntrl: AnimationController,
    private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService,
  ) { }

  ngOnInit() {
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.uid=this.datastorage.user_id;
        this.cart =[];
    return new Promise(resolve =>{
      let body = {
        aksi: 'get_cart', 
        uid: this.uid
      }
      this.accessProviders.postData(body, 'get_cart.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.sum);
           console.log(res.userdata);
           this.userdata=res.userdata;
           this.street=this.userdata.street;
           this.brgy=this.userdata.barangay_name;
           this.cont_num=this.userdata.contact_number;
           this.sum=res.sum;
           console.log(this.sum);
           this.total=parseInt(res.sum)+40;
           for(let datas of res.result){
             this.cart.push(datas);
           }  
           
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  });
  }
  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }  
  buy(){
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.uid=this.datastorage.user_id;
        this.cart =[];
    return new Promise(resolve =>{
      let body = {
        aksi: 'get_cart_to_order', 
        uid: this.uid
      }
      this.accessProviders.postData(body, 'get_cart.php').subscribe((res:any) => {
        if(res.success == true) {
           this.presentToast(res.result); 
           this.router.navigate(['/confirm-order']);
        } else {
          this.presentToast('Failed'); 
        } 
      });   
    });
  });
  }
} 
